.footer .footer__links li { line-height: 1; }
.footer .footer__links li a { font-size: 11px; color: #898989; text-decoration: none; font-family: "MyriadProRegular", sans-serif; font-weight: normal; font-style: normal; }

.copyrights { font-size: 11px; font-family: "MyriadProRegular", sans-serif; font-weight: normal; font-style: normal; color: #898989; }
.copyrights p { margin-bottom: 10px; }

.banner-wide { margin-bottom: 30px; }
.banner-wide img { display: block; width: 100%; }

.categories { margin-bottom: 40px; font-size: 0; }
.categories .categories__item { display: inline-block; vertical-align: top; width: 208px; padding: 15px; margin-left: 46px; border: 1px solid #e8e8e8; text-align: center; text-decoration: none; transition: box-shadow 200ms; }
.categories .categories__item:hover { box-shadow: 0 2px 5px rgba(0, 0, 0, 0.08); }
.categories .categories__item:first-child { margin-left: 0; }
.categories .categories__img { height: 160px; text-align: center; font-size: 0; }
.categories .categories__img:before { content: ""; display: inline-block; vertical-align: middle; width: 0; height: 100%; }
.categories .categories__img img { display: inline-block; vertical-align: middle; max-width: 100%; max-height: 100%; }
.categories .categories__title { font-family: "MyriadProRegular", sans-serif; font-weight: normal; font-style: normal; font-size: 16px; color: #444; margin-bottom: 5px; }
.categories .categories__text { font-family: "MyriadProRegular", sans-serif; font-weight: normal; font-style: normal; font-size: 12px; color: #888; }

.catalog { position: relative; }
.catalog .catalog__topper { *zoom: 1; border: 1px solid #f2f2f2; }
.catalog .catalog__topper:after { content: " "; display: table; clear: both; }
.catalog .catalog__left { float: left; width: 200px; padding: 8px 19px; font-family: "MyriadProRegular", sans-serif; font-weight: normal; font-style: normal; font-size: 16px; color: #7b7a7a; }
.catalog .catalog__sort { float: left; padding: 10px 0; font-size: 0; }
.catalog .catalog__sort li { position: relative; display: inline-block; vertical-align: middle; padding-left: 17px; margin-left: 17px; }
.catalog .catalog__sort li:before { content: "|"; position: absolute; top: 0; left: 0; font-size: 14px; color: #8D9499; }
.catalog .catalog__sort li:first-child { padding-left: 0; }
.catalog .catalog__sort li:first-child:before { content: ""; }
.catalog .catalog__sort li a { font-size: 14px; color: #8d9499; text-decoration: none; transition: color 200ms; }
.catalog .catalog__sort li a:hover, .catalog .catalog__sort li a.active { color: #f78f1e; }
.catalog .catalog__view { float: right; padding: 15px 18px 0 0; }
.catalog .catalog__view ul { font-size: 0; }
.catalog .catalog__view ul li { display: inline-block; vertical-align: middle; margin-left: 12px; }
.catalog .catalog__view ul li:first-child { margin-left: 0; }
.catalog .catalog__view ul li a { display: block; font-size: 12px; color: #615b55; opacity: 0.6; transition: opacity 200ms; }
.catalog .catalog__view ul li a:hover, .catalog .catalog__view ul li a.active { opacity: 1; }
.catalog .catalog__in { *zoom: 1; }
.catalog .catalog__in:after { content: " "; display: table; clear: both; }
.catalog .catalog__filters { float: left; width: 200px; }
.catalog .catalog__list { overflow: hidden; font-size: 0; }
.catalog .catalog__list .item { display: inline-block; vertical-align: top; width: 160px; margin: 0 16px 40px; }

.filters { border: 1px solid #f2f2f2; border-top: none; }
.filters .filters__item { padding: 3px 20px; border-top: 1px solid #f2f2f2; }
.filters .filters__item:first-child { border-top: none; }

.out_wide .content, .out_wide .center { max-width: 1240px; padding-right: 270px; }

.sidebar { position: fixed; top: 0; bottom: 0; width: 240px; background: #fff; border-left: 1px solid #e6e6e6; border-right: 1px solid #e6e6e6; z-index: 10; }
.sidebar .sidebar__in { position: relative; height: 100%; }

.cart { position: relative; height: 100%; padding: 45px 0 105px; }
.cart .cart__topper { position: absolute; top: 0; left: 0; right: 0; height: 45px; padding: 13px 15px 13px 50px; background: #48ab46; text-align: right; font-family: "MyriadProRegular", sans-serif; font-weight: normal; font-style: normal; font-size: 15px; color: #f3f3f3; }
.cart .cart__topper:before { content: ""; position: absolute; top: 12px; left: 15px; font-size: 20px; color: #f3f3f3; }
.cart .cart__wrap { height: 100%; overflow-y: auto; padding: 23px 0; }
.cart .cart__bottom { position: absolute; bottom: 0; left: 0; right: 0; height: 105px; }
.cart .cart__bottom .btn { display: block; height: 44px; border-radius: 0; width: 100%; text-decoration: none; text-align: center; line-height: 44px; font-size: 18px; }
.cart .cart__login { border-top: 1px solid #e6e6e6; padding: 11px 10px; font-size: 14px; color: #727272; font-family: "MyriadProRegular", sans-serif; font-weight: normal; font-style: normal; }
.cart .cart__login a { text-decoration: none; color: #26ade4; }
.cart .cart__list { padding: 0 10px; }
.cart .cart__item { *zoom: 1; margin-top: 40px; }
.cart .cart__item:after { content: " "; display: table; clear: both; }
.cart .cart__item:first-child { margin-top: 0; }
.cart .cart__item .counter { float: left; width: 22px; margin-right: 12px; }
.cart .cart__info { *zoom: 1; }
.cart .cart__info:after { content: " "; display: table; clear: both; }
.cart .cart__img { float: left; width: 65px; }
.cart .cart__img img { max-width: 100%; }
.cart .cart__details { overflow: hidden; font-size: 13px; color: #6d6c6c; }
.cart .cart__title { margin-bottom: 20px; }
.cart .cart__discount { margin-bottom: 20px; color: #fe4365; }
.cart .cart__price { margin-bottom: 15px; font-size: 14px; text-transform: uppercase; }
.cart .cart__add { display: inline-block; font-size: 0; color: #505050; text-decoration: none; background: none; }
.cart .cart__add i, .cart .cart__add span { display: inline-block; }
.cart .cart__add span { font-size: 13px; margin-left: 10px; }
.cart .cart__add i { font-size: 14px; }

.counter { position: relative; text-align: center; }
.counter button { background: none; font-size: 5px; color: #989898; }
.counter .counter__value { padding: 6px 0; font-size: 12px; color: #505050; font-family: "MyriadProRegular", sans-serif; font-weight: normal; font-style: normal; }

.reserve { *zoom: 1; margin-bottom: 18px; }
.reserve:after { content: " "; display: table; clear: both; }
.reserve .reserve__left { position: relative; float: left; text-align: center; width: 66px; }
.reserve .reserve__left i { font-size: 24px; color: #000; }
.reserve .reserve__right { overflow: hidden; font-size: 14px; color: #6d6c6c; font-family: "MyriadProRegular", sans-serif; font-weight: normal; font-style: normal; }
.reserve .reserve__right p { margin-bottom: 10px; }
.reserve .reserve__btn { height: 30px; padding: 0 13px; vertical-align: middle; border-radius: 4px; background: #f78f1e; font-family: "MyriadProRegular", sans-serif; font-weight: normal; font-style: normal; font-size: 14px; color: #fff; }

.total { position: relative; padding: 10px; margin-bottom: 30px; background: #fefcee; }
.total .total__row { *zoom: 1; font-size: 14px; color: #505050; }
.total .total__row:after { content: " "; display: table; clear: both; }
.total .total__row:first-child { margin-bottom: 15px; font-size: 17px; font-family: "MyriadProSemibold"; font-weight: normal; font-style: normal; }
.total .total__col { float: right; }
.total .total__col:first-child { float: left; }

.cart-slider { position: relative; padding: 30px 0; }
.cart-slider .cart-slider__item { *zoom: 1; padding: 0 30px; }
.cart-slider .cart-slider__item:after { content: " "; display: table; clear: both; }
.cart-slider .cart-slider__item > a { display: block; text-decoration: none; }
.cart-slider .cart-slider__img { float: left; width: 65px; }
.cart-slider .cart-slider__img img { max-width: 90%; }
.cart-slider .cart-slider__title { overflow: hidden; font-size: 13px; color: #6d6c6c; }


